<template>
  <v-app>
    <div class="text-center">
    <v-progress-circular
        :size="this.$store.state.spinnerLoading ? 70 : 0"
        :width="10"
        color="purple"
        :indeterminate="this.$store.state.spinnerLoading"
      ></v-progress-circular>
    </div>
    <div class="loader" v-if="this.$store.state.spinnerLoading">  .  .....</div>
    <v-radio-group v-model="row" row column :disabled=this.$store.state.spinnerLoading>
        <v-radio label="1" value="radio-1" @click="interest1" @change="interest1Change" > </v-radio>
        <v-radio label="2" value="radio-2" @click="interest2" @change="interest2Change"> </v-radio>
      </v-radio-group>
    <div v-if="inter1">
      <table width="100%">
        <tr><td>FROM():</td>
          <td>
            <vue-englishdatepicker classValue="datepicker" placeholder="YYYY-MM-DD"
              format="YYYY-MM-DD" @change="changeFromDate" :value="fromdate"/>
          </td>
          <td>TO():</td>
          <td>
            <vue-englishdatepicker classValue="datepicker" placeholder="YYYY-MM-DD"
              format="YYYY-MM-DD" @change="changeToDate" :value="todate" />
          </td>
        </tr>
        </table>
        <table>
          <tr>
            <td><v-checkbox v-model="individual" :label="``" /></td>
            <td><v-checkbox v-model="grossSum" @click="changeGrossSum" :label="``"/></td> 
            <td><v-checkbox v-model="foreigner" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="finance" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="insurance" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="investment" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="bank" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="etcFinance" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="pensionFund" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="government" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="etcCorp" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="etcForeigner" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="privateEquity" @click="changeNotGrossSum(e)" :label="``"/></td>
          </tr>
        </table>
        <v-simple-table>
        <thead>
          <tr>
            <th class="text-left">
              (){{ inOnLftRowLength }}
            </th>
            <th class="text-left">
              ()
            </th>
             <th class="text-left">
               <v-btn
                color="primary"
                elevation="4"
                small
                v-on:click="onBtnExport()"
              >CSV </v-btn>
            </th>
          </tr>
        </thead>
        <!-- <tbody><tr><td></td><td></td></tr></tbody> @selection-changed="getSelectedRows" @row-selected-->
    </v-simple-table>
    </div>
    <div v-if="inter2">
      <table width="100%">
        <tr>
          <td>:</td>
          <td>
            <vue-englishdatepicker classValue="datepicker" placeholder="YYYY-MM-DD"
              format="YYYY-MM-DD" @change="changeSearchDate2" :value="searchDate2"/>
          </td>
          <td>:</td>
          <td :style= "{width: '250px'}">
            <v-select
              v-model="this.$store.state.industries[0]"
              :items="this.$store.state.industries"
              :menu-props="{ maxHeight: '300' }"
              label="Select"
              dense
              full-width
              @change="selectIndustries"
              persistent-hint
              return-object
              single-line
            ></v-select>
          </td>
          <td>
            <v-btn
              icon
              color="primary"
              dark
              @click="searchData">
              <v-icon>mdi-magnify</v-icon>
            </v-btn>
          </td>
        </tr>
      </table>
    </div>
    <ag-grid-vue 
        style="width: 100%; height: 100%;"
        class="ag-theme-alpine"
        :columnDefs="this.$store.state.quickInterestStockLeftGridColumns"
        :rowData="this.$store.state.rowData"
        rowSelection="single"
        @grid-ready="onGridReady"
        @cell-clicked="getSelectedRows"
        :defaultColDef="this.$store.state.defaultColDef"
        >
    </ag-grid-vue>
  </v-app>
</template>
<script>
import { AgGridVue } from "ag-grid-vue";
import VueEnglishdatepicker from 'vue-englishdatepicker';
import {mapGetters, mapState, mapMutations, mapActions} from 'vuex';

export default {
  computed: {
    ...mapGetters(['inOnLftRowLength', 'getStkNm']),
    /**  
     * ...mapGetters({
     *    lth: 'inOnLftRowLength', //   , 'inOnLftRowLength' lth .
     *    stockName: 'getStkNm', //   , 'getStkNm' stockName .
     * })
     */
    ...mapState(['inOnLftRowData', 'rowData'])
  },
  created() {
     console.log('created');
  },
  beforeMount() {
    console.log('beforeMount');
    this.todate = this.$moment(new Date()).format('YYYY-MM-DD');
    this.fromdate = this.$moment(new Date()).add(-7, 'days').format('YYYY-MM-DD');
    this.searchDate2 = this.$moment(new Date()).format('YYYY-MM-DD');
    this.$store.state.inOnLftRowData = [];
    this.$store.dispatch('emptyRowData', []); // dispatch (   .)
    this.$store.dispatch('callQuickInterestTwoIndustrySelectBox', []);
  },
  mounted(){
    //   
    /**  
     * //  
     * EventBus.$on('signUp', users => {
     *   this.allUsers.push(users);
     * });
     */

    /**
     * //  
     * EventBus.$emit('signUp', userObj);
     */
  },
  name: 'QuickInterestStock',
  methods: {
    async selectIndustries(e){
      console.log(e);
      this.$store.commit('setSendIndustry', e); 
    }
    , async searchData(){
        let num = this.searchDate2.replace(/-/gi, "").toString();
        if( isNaN(parseInt(this.searchDate2.replace(/-/gi, ""))) 
            || !Number(this.searchDate2.replace(/-/gi, ""))
            || num.length != 8) {
          alert('2   !');
          return;
        }

        if( this.$store.state.sendIndustry === null ||
            this.$store.state.sendIndustry === undefined || 
            this.$store.state.sendIndustry === ''){
          alert(' !');
          return;
        }

        try{
          let postData = { 
            searchDate2: this.searchDate2
            , category: 'interestTwo'
            , industry: this.$store.state.sendIndustry
          };
          await this.$store.dispatch('callQuickInterestTwoStockLeft', postData);
        } catch(error){
          console.log(error);
        }
    }
     , onBtnExport() {
      this.gridApi.exportDataAsCsv(); 
     },
    ...mapMutations(['callInOnLftRowData']), // 'emptyRowData'
    ...mapActions(['emptyRowData']),
    keyFromDate(e){
      console.log('keyUpFromDate e;;;'+e);
    },
    changeNotGrossSum(e){
      console.log('changeNotGrossSum e;;;'+e);
      if(this.finance && this.foreigner && this.finance && this.insurance
      && this.investment && this.bank && this.etcFinance && this.pensionFund
      && this.government && this.etcCorp && this.etcForeigner && this.privateEquity){
        this.grossSum = true;
        return;
      }
      if(!e){
        this.grossSum = false;
        return;
      }
    },
    changeGrossSum(){
      this.foreigner = this.grossSum;
      this.finance = this.grossSum;
      this.insurance = this.grossSum;
      this.investment = this.grossSum;
      this.bank = this.grossSum;
      this.etcFinance = this.grossSum;
      this.pensionFund = this.grossSum;
      this.government = this.grossSum;
      this.etcCorp = this.grossSum;
      this.etcForeigner = this.grossSum;
      this.privateEquity = this.grossSum;
    },
    changeFromDate(e){
      this.fromdate = e;
    },
    changeToDate(e){
      this.todate = e;
    }
    , async interest1() {
        this.inter1 = true;
        this.inter2 = false;
        if(this.fromdate === undefined){
          let date = new Date();
          let year = date.getFullYear();
          let month = ("0" + (1 + date.getMonth())).slice(-2);
          let day = ("0" + date.getDate()).slice(-2);
          this.fromdate = year + '-'+month + '-'+day;
          // console.log('interest1 undefined this.fromdate', this.fromdate);
        }
        if(this.todate === undefined){
          let date = new Date();
          let year = date.getFullYear();
          let month = ("0" + (1 + date.getMonth())).slice(-2);
          let day = ("0" + date.getDate()).slice(-2);
          this.todate = year + '-'+ month +'-'+ day;
          // console.log('interest1 undefined this.todate', this.todate);
        } 
        if(parseInt(this.fromdate.replace(/-/gi, "")) > parseInt(this.todate.replace(/-/gi, ""))){
          alert('toDate fromDate   . \n !');
          return;
        }
        let postData = {
          fromdate: this.fromdate, 
          todate: this.todate,
          checkbx: {
            individual: this.individual,
            grossSum: this.grossSum,
            foreigner: this.foreigner,
            finance: this.finance,
            insurance: this.insurance,
            investment: this.investment,
            bank: this.bank,
            etcFinance: this.etcFinance,
            pensionFund: this.pensionFund,
            government: this.government,
            etcCorp: this.etcCorp,
            etcForeigner: this.etcForeigner,
            privateEquity: this.privateEquity,
          },
          category: 'interestOne',
        };
        await this.$store.commit('callInOnLftRowData', []);
        // this.$store.state.rowData = []; // mapState  , this.rowData;
        // this.emptyRowData([]);
        // this.$store.commit('emptyRowData', []); // mapMutations  , this.emptyRowData([]);
        //   , dispatch  .
        await this.$store.dispatch('emptyRowData', []); // mapActions  , this.emptyRowData([]);
        try {
          await this.$store.dispatch('callQuickInterestStockLeft', postData);
        } catch(error){
          console.log(error);
        }
    },

    interest1Change(){
      console.log('inter1 changed...');
    },

    interest2Change(){
      console.log('inter2 changed...');
    },

    async interest2() {
      this.inter1 = false;
      this.inter2 = true;
      console.log("interest2()...");
    },

    onGridReady(params) {
      this.gridApi = params.api;
      this.columnApi = params.columnApi;
      // this.gridApi.sizeColumnsToFit();
    },
    async getSelectedRows() {
      const selectedNodes = this.gridApi.getSelectedNodes();
      const selectedData = selectedNodes.map(node => node.data);
      const csvFileName = selectedData[0].fileTitle;
      const stockName = selectedData[0].stockName;
      try {
        await this.$store.dispatch('callStockRight', { 
          csvFileName
          , stockName
          , fromdate: this.fromdate
          , todate: this.todate
          , category: 'quickOne'
        });
        await this.$store.dispatch('callShortSelling', { 
          csvFileName
          , stockName
          , fromdate: this.fromdate
          , todate: this.todate
          , category: 'quickOne'
        });
        await this.$store.dispatch('callLoanTransaction', { 
          csvFileName
          , stockName
          , fromdate: this.fromdate
          , todate: this.todate
          , category: 'quickOne'
        });
      } catch(error){
        console.log(error);
      }
    },
  },
  components: {
    AgGridVue,
    VueEnglishdatepicker,
  },
  data: () => ({
    // ag grid 
    columnDefs: null,
    
    // 1  
    inter1: false,
    // 1 : 
    individual: false,
    // 1 : 
    grossSum: true,
    // 1 : 
    foreigner: true,
    // 1 : 
    finance: true,
    // 1 : 
    insurance: true,
    // 1 : 
    investment: true,
    // 1 : 
    bank: true,
    // 1 : 
    etcFinance: true,
    // 1 : 
    pensionFund: true,
    // 1 : 
    government: true,
    // 1 : 
    etcCorp: true,
    // 1 : 
    etcForeigner: true,
    // 1 : 
    privateEquity: true,
    // ///////////////////////////////////////////////////
    // 2  
    inter2: false,
    
    
  }),
}
</script>
